.sidebarOption {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.sidebarOption:hover {
    background-color: #e8f5fe;
    border-radius: 30px;
    color: var(--twitter-color);
    transition: color 100ms ease-out;
}

.sidebarOption > .MuiSvgIcon-root {
    padding: 15px;
}

.sidebarOption > h2{
    font-size: 20px;
    font-weight: 750;
    margin-right: 20px;
    padding-bottom: 10px;
}
.sidebarOption--active {
    color: var(--twitter-color);
}
.sidebarOption_icon {
    font-size: 30px !important;
}